.SearchForm_root {
	display: flex;
	position: absolute;
	top: 8.5px;
	left: 30%;
	width: 45%;
	height: 39px;
}

.SearchForm_input {
	position: relative;
	width: 100%;
	margin-right: 5px;
	padding: 10.5px 20px;
	border: 0;
	border-radius: 39px;
	font-size: 15px;
	background: #efefef;
	color: #455860;
}

.SearchForm_input:hover, .SearchForm_input:focus {
	background: #eaeaea;
}

.SearchForm_input:focus {
	outline: 0;
}

.SearchForm_submit {
	position: relative;
	padding: 8.5px;
	border-radius: 39px;
	background: #fdfdfd;
}

.SearchForm_icon {
	font-size: 20px;
	color: #444;
}

.SearchForm_submit:hover {
	cursor: pointer;
}

.SearchForm_submit:focus {
	outline: 0;
}

.Header_root {
  position: relative;
  height: 56px;
  box-sizing: border-box;
  border-bottom: solid 1px #ddd;
}

.Header_logo {
  position: absolute;
  top: calc((56px - 21px) / 2);
  left: 5%;
  width: 92px;
  height: 21px;
}

.Header_logo * {
  position: absolute;
  width: 100%;
  height: 100%;
}

.Header_menu {
  display: inline-block;
  position: absolute;
  top: calc((56px - 34px) / 2);
  right: 5%;
  height: 34px;
  padding: 0 15px;
  box-sizing: border-box;
  line-height: 34px;
  font-size: 15px;
  font-weight: bold;
  text-decoration: none;
  background: #668ad8;
  color: #FFF;
  box-shadow: 0px 0px 0px 3px #668ad8;
  border: dashed 1px #FFF;
  border-radius: 5px;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

.Header_menu:hover, .Header_menu:active {
  border: dotted 1px #FFF;
}
.Item_root {
    position: relative;
    width: 32% !important;
    margin-top: 0 !important;
    margin-top: 10px !important;
    margin-right: 10px !important;
}
.ItemList_root {
    position: relative;
    padding: 30px 0;
}

.ItemList_title {
    margin-top: 0;
    text-decoration: underline;
}

.ItemList_content {
    display: flex;
    flex-wrap: wrap;
}

.Tag_root {
  position: relative;
  height: 40px;
  margin-bottom: 10px;
  margin-right: 5px;
  padding: 10px 20px;
  box-sizing: border-box;
  border: solid 1px #ddd;
  border-radius: 5px;
  line-height: 20px;
  font-size: 15px;
  -webkit-transition: 0.3s;
  transition: 0.3s;
}

.select {
  border: solid 1px orchid;
}

.Tag_root:hover {
  cursor: pointer;
}

.TagList_root {
  position: relative;
  padding-top: 30px;
}

.TagList_header {
  position: relative;
  width: 100%;
}

.TagList_header span {
  font-size: 17px;
  text-decoration: underline;
}

.TagList_body {
  position: relative;
  display: flex;
  width: 100%;
  padding: 10px 0 0 0;
  flex-wrap: wrap;
}

.TopContainer {
    position: relative;
    width: 100%;
    padding: 0 5% 0 5%;
}
html {
  width: 100%;
}

body {
  margin: 0;
  padding: 0;
}

.App {
  position: absolute;
  width: 100%;
}
